@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff"); }

.wrapper {
  box-sizing: border-box;
  max-width: 1280px;
  margin: 0 auto; }

.visually-hidden {
  position: absolute;
  transform: scale(0); }

.button {
  box-sizing: border-box;
  background: none;
  border: none;
  cursor: pointer; }

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%); }

.header__container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 30px 90px 35px 30px; }

.header__contacts {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 27px;
  padding-top: 7px;
  margin-right: -7px; }
  @media (max-width: 1023px) {
    .header__contacts span {
      position: absolute;
      transform: scale(0); } }

@media (max-width: 1023px) {
  .header__logo-words {
    position: absolute;
    transform: scale(0); } }

@media (max-width: 767px) {
  .header__logo img {
    width: 35px;
    height: 35px; } }

.header p {
  color: #ffffff; }

.header a {
  text-decoration: none;
  border-radius: 3px; }
  .header a:hover {
    box-shadow: 0 0 0 4px #ffffff; }
  .header a:active {
    box-shadow: 0 0 0 1px #ffffff;
    opacity: 0.3; }

.header__callback-button {
  margin-top: 5px; }
  @media (max-width: 1023px) {
    .header__callback-button {
      display: none; } }

.logo {
  display: grid;
  grid-template-columns: min-content max-content; }

.logo span {
  display: block;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.7);
  margin-top: 1px; }

.logo p {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  line-height: 29px;
  font-weight: 700;
  color: #f2f5f6;
  margin: 0; }

.logo__words {
  padding-top: 5px;
  margin-left: 15px; }

.contact {
  display: grid;
  grid-template-columns: min-content 1fr;
  gap: 15px;
  padding: 8px; }
  .contact::before {
    content: "";
    display: block;
    width: 21px;
    height: 20px;
    background: #00b2ff; }
    @media (max-width: 1023px) {
      .contact::before {
        width: 20px;
        height: 20px; } }
  .contact--email::before {
    mask: no-repeat center/cover url("../img/svg/contacts-sprite.svg#email"); }
  .contact--phone::before {
    mask: no-repeat center/cover url("../img/svg/contacts-sprite.svg#phone"); }

.contact span {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #f2f5f7;
  text-decoration: none; }

.callback-button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  color: #f2f5f6;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  min-height: 40px;
  max-width: 160px;
  width: 100%;
  padding: 7px 15px 9px;
  border-radius: 3px;
  border: solid 1px #f2f5f6; }

.footer {
  padding: 50px 0 55px;
  background: #041827; }

.footer__wrapper {
  padding: 0 30px;
  display: flex; }

.footer__accordion {
  display: flex; }

.footer__logo {
  margin-bottom: 30px; }

.social {
  max-width: 150px; }

.social a {
  display: block;
  line-height: 0;
  padding: 5px; }

.social__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, 40px);
  gap: 15px;
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0; }

.navigation h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  font-weight: 400;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0 0 20px; }

.navigation a {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none; }

.navigation__list {
  display: grid;
  grid-template: repeat(4, min-content)/repeat(2, 220px);
  column-gap: 30px;
  row-gap: 10px;
  grid-auto-flow: column dense;
  list-style: none;
  margin: 0;
  padding: 0; }

.navigation__item {
  word-break: break-all; }

@media (min-width: 768px) {
  .accordion__control {
    display: none; } }

.footer-contacts {
  display: grid;
  grid-template-columns: 1fr; }
  .footer-contacts h2 {
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-size: 15px;
    line-height: 16px;
    font-weight: 400;
    letter-spacing: 0.8px;
    color: #ffffff;
    text-transform: uppercase;
    margin: 0; }
  .footer-contacts p,
  .footer-contacts a {
    font-family: "PT Mono", "Arial", sans-serif;
    color: rgba(255, 255, 255, 0.7);
    margin: 0;
    text-decoration: none; }

.top-banner {
  box-sizing: border-box;
  min-height: 880px;
  padding-top: 305px;
  background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), no-repeat center/cover url("../img/main/topbackground-l.jpg"), #041827; }
  @media (min-resolution: 1.5dppx) {
    .top-banner {
      background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), no-repeat center/cover url("../img/main/topbackground-l@2x.jpg"), #041827; } }
  @media (min-width: 768px) and (max-width: 1023px) {
    .top-banner {
      padding-top: 180px;
      background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), no-repeat center/cover url("../img/main/topbackground-m.jpg"), #041827; } }
  @media (min-width: 768px) and (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .top-banner {
      background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), no-repeat center/cover url("../img/main/topbackground-m@2x.jpg"), #041827; } }
  @media (max-width: 767px) {
    .top-banner {
      padding-top: 150px;
      background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), no-repeat center/cover url("../img/main/topbackground-s.jpg"), #041827; } }
  @media (max-width: 767px) and (min-resolution: 1.5dppx) {
    .top-banner {
      background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), no-repeat center/cover url("../img/main/topbackground-s@2x.jpg"), #041827; } }

.top-banner__grid {
  display: flex;
  flex-direction: column; }
  @media (min-width: 1024px) {
    .top-banner__grid {
      padding-left: 155px; } }

.top-banner h2,
.top-banner p,
.top-banner a {
  margin: 0; }

.top-banner h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  line-height: 94px;
  font-weight: 700;
  letter-spacing: 5.7px;
  color: #f2f5f7;
  text-transform: uppercase;
  margin-bottom: 30px; }
  @media (min-width: 768px) and (max-width: 1023px) {
    .top-banner h2 {
      font-size: 64px;
      line-height: 75px; } }
  @media (max-width: 767px) {
    .top-banner h2 {
      font-size: 36px;
      line-height: 42px; } }

.top-banner p {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 22px;
  line-height: 35px;
  font-weight: 400;
  color: #f2f5f7;
  text-transform: lowercase;
  max-width: 640px;
  margin-bottom: 46px; }
  @media (max-width: 767px) {
    .top-banner p {
      font-size: 14px;
      line-height: 22px; } }

.top-banner a {
  box-sizing: border-box;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  line-height: 21px;
  font-weight: 700;
  letter-spacing: 1px;
  color: #f2f5f7;
  text-transform: uppercase;
  text-decoration: none;
  padding: 15px 20px 15px 26px;
  background: #00b2ff;
  border-radius: 3px;
  max-width: 410px;
  width: 100%;
  min-height: 55px; }
  .top-banner a:hover {
    opacity: 0.3; }
  .top-banner a:active {
    box-shadow: 0 0 0 2px #f2f5f7; }
  @media (min-width: 768px) and (max-width: 1023px) {
    .top-banner a {
      margin-right: 45px; } }
  @media (max-width: 767px) {
    .top-banner a {
      font-size: 14px;
      line-height: 17px; } }

.advantages {
  padding-top: 120px; }

.advantages__container {
  padding: 0 28px 0 30px; }

.advantages__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 29px;
  column-gap: 32px;
  list-style: none;
  padding: 0;
  margin: 0; }
  @media (max-width: 767px) {
    .advantages__list {
      grid-template-columns: 1fr;
      gap: 30px; } }

.advantages-card h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  line-height: 34px;
  font-weight: 400;
  color: #00b2ff;
  margin: 0 0 14px 0; }
  @media (max-width: 767px) {
    .advantages-card h3 {
      font-size: 20px;
      line-height: 28px; } }

.advantages-card p {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #041827;
  margin: 0 0 20px 0; }
  @media (max-width: 767px) {
    .advantages-card p {
      font-size: 14px;
      line-height: 19px; } }

.advantages-card {
  box-sizing: border-box;
  max-width: 595px;
  min-height: 300px;
  height: 100%;
  padding: 40px 45px 35px;
  box-shadow: inset 0 0 0 1px #ebebeb, inset 5px 0 0 0 #00b2ff; }
  .advantages-card::before {
    content: "";
    display: block;
    background: #00b2ff;
    width: 35px;
    height: 35px;
    margin-bottom: 22px; }
    @media (max-width: 767px) {
      .advantages-card::before {
        width: 30px;
        height: 30px; } }
  .advantages-card--time::before {
    mask: no-repeat center/cover url("../img/svg/advantages-sprite.svg#time"); }
  .advantages-card--suppliers::before {
    mask: no-repeat center/cover url("../img/svg/advantages-sprite.svg#suppliers"); }
  .advantages-card--confidential::before {
    mask: no-repeat center/cover url("../img/svg/advantages-sprite.svg#confidential"); }
  .advantages-card--personal::before {
    mask: no-repeat center/cover url("../img/svg/advantages-sprite.svg#personal"); }
  .advantages-card:hover {
    background: #00b2ff; }
    .advantages-card:hover::before {
      background: #ffffff; }
    .advantages-card:hover h3,
    .advantages-card:hover p {
      color: #ffffff; }

